<template>
    <div class="shopTable">

        <div class="table-layout-container" id="table-layout-container">
            <div style="z-index:20;width:100%;height:100%;text-align:center;position: absolute;margin-top:50px;"
                 v-if="serverReady">
                <v-progress-circular
                    :size="40"
                    color="#00BCD4"
                    indeterminate
                ></v-progress-circular>
            </div>
            <div v-if="fixed>0">
                <div class="table-container-header" :style="{'top':stickTop}">
                    <div v-if="fixed>0" :style="{'min-width':fixedWidth+'px','width':fixedWidth+'px'}">
                        <table class="table" cellpadding="0" cellspacing="0" border="0">
                            <thead>
                            <tr>
                                <th v-for="(item,index) in fixedHeaderDatas" :style="item.style">
                                    <div v-if="item.type=='allCheckbox'">
                                        <div class="radio-box"
                                             style="max-height:20px;margin:-15px auto 0 auto;padding:0;width:60px;z-index:70;position: relative;">
                                            <v-checkbox
                                                dense
                                                v-model="allCheck"
                                                :label="item.name"
                                            ></v-checkbox>
                                        </div>
                                    </div>
                                    <div v-else class="header-item">
                                        {{ item.name }}
                                        <span v-if="item.sort" class="sort-box">
                                    <font-awesome-icon :class="'icon '+item.sortDesc" icon="fa-solid fa-arrow-up-long"
                                                       @click="fixHeaderSort(index,'desc')"/>
                                    <font-awesome-icon :class="'icon '+item.sortAsc" icon="fa-solid fa-arrow-down-long"
                                                       @click="fixHeaderSort(index,'asc')"/>
                                </span>
                                    </div>
                                </th>
                            </tr>
                            </thead>
                        </table>
                    </div>
                    <div class="header-scroll-table" style="border-left:solid 2px #666;position: relative"
                         id="headerTargetTable">
                        <div id="scrollBarHander" :style="scrollWidth"></div>
                        <table class="table" cellpadding="0" cellspacing="0" border="0" :style="scrollWidth">
                            <thead>
                            <tr>
                                <th v-for="(item,index) in headerDatas" :style="item.style">
                                    <div class="header-item">
                                        {{ item.name }}
                                        <span v-if="item.sort" class="sort-box">
                                     <font-awesome-icon :class="'icon '+item.sortDesc" icon="fa-solid fa-arrow-up-long"
                                                        @click="headerSort(index,'desc')"/>
                                    <font-awesome-icon :class="'icon '+item.sortAsc" icon="fa-solid fa-arrow-down-long"
                                                       @click="headerSort(index,'asc')"/>
                                    </span>
                                    </div>
                                </th>
                            </tr>
                            </thead>
                        </table>
                    </div>
                </div>

                <div class="table-container">
                    <div class="table-fixed-box" v-if="fixed>0"
                         :style="{'min-width':fixedWidth+'px','width':fixedWidth+'px'}">
                        <table class="table" cellpadding="0" cellspacing="0" border="0">
                            <tbody v-if="fixedGridList.length>0">
                            <tr v-for="(items,index) in fixedGridList">
                                <td v-for="item in items" :style="item.attr.style">
                                    <div v-if="item.attr.type=='image'" :style="item.attr.textStyle">
                                        <img :src="item.value">
                                    </div>
                                    <div v-else-if="item.attr.type=='button'" :style="item.attr.textStyle">
                                        <v-btn depressed class="white-button"
                                               @click="buttonFixedAction(index,item.attr)">

                                            <span>{{ item.attr.name }}</span></v-btn>
                                    </div>
                                    <div v-else-if="item.attr.type=='underline-action'"
                                         class="overflow-hidden underline" :style="item.attr.textStyle"
                                         @click="buttonFixedAction(index,item.attr)">
                                        {{ item.value }}
                                    </div>
                                    <div v-else-if="item.attr.type=='allCheckbox'">
                                        <div class="radio-box"
                                             style="max-height:30px;margin:-7px auto 0 auto;padding:0;width:20px;">
                                            <v-checkbox
                                                dense
                                                v-model="checkList"
                                                :value="item.value"
                                            ></v-checkbox>
                                        </div>
                                    </div>
                                    <div v-else :style="item.attr.textStyle" class="overflow-hidden">
                                        {{ item.value }}
                                    </div>
                                </td>
                            </tr>
                            </tbody>
                            <tbody v-else>
                            <tr>
                                <td :style="{'width':fixedWidth+'px'}">
                                    <div class="emptyBox">
                                    </div>
                                </td>
                            </tr>
                            </tbody>
                        </table>
                        <div id="fixed-divide"></div>
                    </div>
                    <div class="overflow-auto-box" style="border-left:solid 2px #666;" id="targetTable">
                        <table class="table" cellpadding="0" cellspacing="0" border="0" :style="scrollWidth">
                            <tbody v-if="gridList.length>0">
                            <tr v-for="(items,index) in gridList">
                                <td v-for="item in items" :style="item.attr.style">
                                    <div v-if="item.attr.type=='image'" :style="item.attr.textStyle">
                                        <img :src="item.value">
                                    </div>
                                    <div v-else-if="item.attr.type=='underline-action'" :style="item.attr.textStyle"
                                         @click="buttonAction(index,item.attr)" class="overflow-hidden underline">
                                        {{ item.value }}
                                    </div>
                                    <div v-else-if="item.attr.type=='button'" :style="item.attr.textStyle">
                                        <v-btn depressed class="white-button" @click="buttonAction(index,item.attr)">
                                            <span>{{ item.attr.name }}</span></v-btn>
                                    </div>
                                    <div v-else :style="item.attr.textStyle" class="overflow-hidden">
                                        {{ item.value }}
                                    </div>
                                </td>
                            </tr>
                            </tbody>
                            <tbody v-else>
                            <tr>
                                <td>
                                    <div class="emptyBox" style="text-align:left;padding-left:150px;">
                                        데이타가 존재하지 않습니다
                                    </div>
                                </td>
                            </tr>
                            </tbody>
                        </table>
                    </div>
                </div>
            </div>
            <div v-else>
                <div class="table-container">
                    <table class="table" cellpadding="0" cellspacing="0" border="0" style="width:100%;">
                        <thead style="position: sticky;top:0;">
                        <tr>
                            <th v-for="(item,index) in headerDatas" :style="item.style">
                                <div v-if="item.type=='allCheckbox'">
                                    <div class="radio-box"
                                         style="max-height:20px;margin:-15px auto 0 auto;padding:0;width:60px;z-index:70;position: relative;">
                                        <v-checkbox
                                            dense
                                            v-model="allCheck"
                                            :label="item.name"
                                        ></v-checkbox>
                                    </div>
                                </div>
                                <div v-else class="header-item">
                                    {{ item.name }}
                                    <span v-if="item.sort" class="sort-box">
                                    <font-awesome-icon :class="'icon '+item.sortDesc" icon="fa-solid fa-arrow-up-long"
                                                       @click="headerSort(index,'desc')"/>
                                    <font-awesome-icon :class="'icon '+item.sortAsc" icon="fa-solid fa-arrow-down-long"
                                                       @click="headerSort(index,'asc')"/>
                                </span>
                                </div>
                            </th>
                        </tr>
                        </thead>
                        <tbody v-if="gridList.length>0">
                        <tr v-for="(items,index) in gridList">
                            <td v-for="item in items">
                                <div v-if="item.attr.type=='image'" :style="item.attr.textStyle">
                                    <img :src="item.value">
                                </div>
                                <div v-else-if="item.attr.type=='underline-action'" :style="item.attr.textStyle"
                                     @click="buttonAction(index,item.attr)" class="overflow-hidden underline">
                                    {{ item.value }}
                                </div>
                                <div v-else-if="item.attr.type=='allCheckbox'">

                                    <div class="radio-box"
                                         style="max-height:30px;margin:-7px auto 0 auto;padding:0;width:20px;">
                                        <v-checkbox
                                            dense
                                            v-model="checkList"
                                            :value="item.value"
                                        ></v-checkbox>
                                    </div>
                                </div>
                                <div v-else-if="item.attr.type=='button'" :style="item.attr.textStyle">
                                    <v-btn depressed class="white-button" @click="buttonAction(index,item.attr)">
                                        <span>{{ item.attr.name }}</span></v-btn>
                                </div>
                                <div v-else  class="overflow-hidden">
                                    {{ item.value }}
                                </div>
                            </td>
                        </tr>
                        </tbody>
                        <tbody v-else>
                        <tr>
                            <td :colspan="headerDatas.length">
                                <div class="emptyBox">
                                    데이타가 존재하지 않습니다
                                </div>
                            </td>
                        </tr>
                        </tbody>
                    </table>
                </div>
            </div>
        </div>
        <div class="paging-row" v-if="pagingView">
            <ul class="paging-ul">
                <li class="d-page" @click="pagingMove('first')">
                    <font-awesome-icon icon="fa-solid fa-angles-left"/>
                </li>
                <li class="d-page" @click="pagingMove('prev')">
                    <font-awesome-icon icon="fa-solid fa-angle-left"/>
                </li>
                <li :class="item.class" v-for="item in pagingLinks" @click="pagingDefaultMove(item.label)">
                    {{ item.label }}
                </li>
                <li class="d-page" @click="pagingMove('next')">
                    <font-awesome-icon icon="fa-solid fa-angle-right"/>
                </li>
                <li class="d-page" @click="pagingMove('last')">
                    <font-awesome-icon icon="fa-solid fa-angles-right"/>
                </li>
            </ul>
        </div>
    </div>
</template>

<script>
import vueCustomScrollbar from 'vue-custom-scrollbar'
import "vue-custom-scrollbar/dist/vueScrollbar.css"
import JQ from "jquery"

export default {
    name: 'shopTable',
    components: {
        vueCustomScrollbar
    },
    props: ["datas", "fixDatas", "fixed", "columns", "paging", "updateStickyTop", "fixType", "fixData","pagingReload"],
    data() {
        return {
            fixedHeaderDatas: [],
            headerDatas: [],
            fixedGridList: [],
            gridList: [],
            targetX: '',
            scrollMove: false,
            scrollFirstX: 0,
            scrollWidthNumber: 0,
            pagingLinks: [],
            pagingView: false,
            nextPage: '',
            prevPage: '',
            lastPage:'',
            allCheck: false,
            checkList: [],
            serverReady: true,
            stickTop: 0,
            divideMove: false,
            divideFirstX: 0,
            fixedWidth: '',
            orgFixedWidth: '',
        };
    },
    watch: {
        pagingReload(val) {
            if(val) {
                this.allCheck = false;
                this.checkList = [];
                this.getPagingData();
                this.setPaging();
            }
        },
        datas() {
            if(this.pagingReload)return;
            this.allCheck = false;
            this.checkList = [];
            this.getPagingData();
            this.setPaging();
        },
        allCheck(val) {
            this.checkList = [];
            if (val) {
                if (this.fixType == 'rows') {

                    for (let item of this.fixDatas) {
                        this.checkList.push(item.id);
                    }
                } else {
                    for (let item of this.datas) {
                        this.checkList.push(item.id);
                    }
                }
            }
        },
        checkList(datas) {
            let ids = {};
            for (let id of datas) {
                ids[id] = id;
            }
            this.$emit('checkDataList', ids);
        }
    },
    created() {
        this.stickTop = this.updateStickyTop;
        this.getData();
        this.setPaging();
    },
    methods: {
        getData() {

            if (this.fixed > 0) {
                this.getScrollData()
            } else {
                this.getNormalData()
            }
        },
        getNormalData() {
            this.headerDatas = []
            for (let item of this.columns) {
                if (item.sort) {
                    item.sortAsc = ''
                    item.sortDesc = ''
                }
                item.style = (item.width) ? {'width': item.width + 'px'} : {};
                this.headerDatas.push(item);

            }
            for (let items of this.datas) {
                let itemData = [];
                for (let data of this.columns) {
                    let gridItem = {value: items[data.id]};
                    data.style = (data.width) ? {'min-width': data.width + 'px','width': data.width + 'px'} : {};
                    if (!data.textStyle) {
                        data.textStyle = {};
                    }

                    gridItem.attr = data;
                    itemData.push(gridItem)

                }
                this.gridList.push(itemData)
            }
            this.serverReady = false;
        },
        getScrollData() {
            this.headerDatas = []
            this.fixedHeaderDatas = [];
            let headerIndex = 0;

            let scrollWidth = 0;
            let fixedWidth = 0;
            for (let item of this.columns) {
                if (item.sort) {
                    item.sortAsc = ''
                    item.sortDesc = ''
                }
                item.style = (item.width) ? {'min-width': item.width + 'px'} : {};
                if (this.fixed > 0 && headerIndex < this.fixed) {
                    this.fixedHeaderDatas.push(item);
                    fixedWidth = fixedWidth + parseFloat((item.width) ? item.width : 150);
                } else {
                    scrollWidth = scrollWidth + parseFloat((item.width) ? item.width : 150);
                    this.headerDatas.push(item);
                }
                headerIndex++;
            }
            this.fixedWidth = fixedWidth;
            this.orgFixedWidth = fixedWidth;
            if (this.fixed > 0) {
                this.scrollWidthNumber = scrollWidth;
                this.scrollWidth = {'min-width': scrollWidth + 'px'};
            }
            this.fixedGridList = [];
            this.gridList = [];
            if (this.fixType == 'rows') {
                this.setFixData();
            } else {
                this.setData();
            }


            this.serverReady = false;
        },
        setFixData() {
            for (let items of this.fixDatas) {
                let itemData = [];
                let gridIndex = 0;
                for (let data of this.columns) {
                    if (parseInt(this.fixed) > 0 && gridIndex < parseInt(this.fixed)) {
                        let gridItem = {value: items[data.id]};
                        const rows = (items.rows) ? items.rows : 1;
                        let height = rows * 40;
                        let attrData = {};
                        attrData.textStyle = {};
                        if (data.width) {
                            attrData.style = {'min-width': data.width + 'px','width': data.width + 'px', 'height': height + 'px'};

                        } else {
                            attrData.style = {'height': height + 'px'};
                        }


                        attrData.textStyle = (data.textStyle) ? data.textStyle : {};
                        if (data.width) {
                            let innerWidth = parseInt(data.width)-5;
                            attrData.textStyle['width'] = innerWidth+'px';
                        }
                        attrData.type = data.type;
                        attrData.action = data.action;
                        attrData.name = data.buttonName;
                        attrData.actionId = data.actionId;
                        gridItem.attr = attrData;
                        itemData.push(gridItem)
                    }
                    gridIndex++;
                }
                this.fixedGridList.push(itemData);
            }
            for (let items of this.datas) {
                let itemData = [];
                let gridIndex = 0;
                for (let data of this.columns) {
                    if (parseInt(this.fixed) > 0 && gridIndex >= parseInt(this.fixed)) {
                        let gridItem = {value: items[data.id]};
                        if (!data.textStyle) {
                            data.textStyle = {};
                        }
                        data.style = (data.width) ? {'min-width': data.width + 'px','width': data.width + 'px'} : {};
                        gridItem.attr = data;
                        itemData.push(gridItem)
                    }
                    gridIndex++;

                }
                this.gridList.push(itemData)
            }
            this.serverReady = false;
        },
        setData() {
            for (let items of this.datas) {
                let itemData = [];
                let fixedItemData = [];
                let gridIndex = 0;
                for (let data of this.columns) {

                    let gridItem = {value: items[data.id]};
                    if (!data.textStyle) {
                        data.textStyle = {};
                    }
                    if (data.width) {
                        let innerWidth = parseInt(data.width)-5;
                        data.textStyle['width'] = innerWidth+'px';
                    }

                    data.style = (data.width) ? {'min-width': data.width + 'px','width': data.width + 'px'} : {};
                    gridItem.attr = data;
                    if (parseInt(this.fixed) > 0 && gridIndex < parseInt(this.fixed)) {

                        fixedItemData.push(gridItem)
                    } else {
                        itemData.push(gridItem)
                    }
                    gridIndex++;
                }
                this.fixedGridList.push(fixedItemData);
                this.gridList.push(itemData)
            }
            this.serverReady = false;
        },
        getPagingData() {
            this.fixedGridList = [];
            this.gridList = [];
            if (this.fixType == 'rows') {
                this.setFixData();
            } else {
                this.setData();
            }
        },
        buttonAction(index, attr) {
            const id = (attr.actionId) ? this.datas[index][attr.actionId] : this.datas[index].id;
            this.$emit(attr.action, {id: id});

        },
        buttonFixedAction(index, attr) {
            if (this.fixType == 'rows') {
                const id = (attr.actionId) ? this.fixDatas[index][attr.actionId] : this.fixDatas[index].id;
                this.$emit(attr.action, {id: id});
            } else {
                this.buttonAction(index, attr)

            }


        },
        setPaging() {
            if (this.paging && this.paging.currentPage) {
                if(this.paging.total<1) {
                    this.pagingView = false;
                    this.pagingLinks = [];

                    return;
                }
                this.pagingView = true;
                this.pagingLinks = [];

                const pagingGroup =  this.paging.total/this.paging.limit;
                const pagingEnd = Math.ceil(pagingGroup);

                let stIndex =  this.paging.currentPage/this.paging.pageGroup;
                const checkIndex = this.paging.currentPage % this.paging.pageGroup;
                stIndex =  Math.floor(stIndex);
                if(checkIndex<1) {
                    stIndex = stIndex -1;
                }
                const startIndex = (stIndex * this.paging.pageGroup)+1;
                let endIndex = startIndex+this.paging.pageGroup;
                let nexPage = 0;
                for (let index=startIndex; index<endIndex; index++) {
                    if(index <= pagingEnd) {
                        const item = {};
                        item.class = (this.paging.currentPage == index) ? 'active' : '';
                        item.label = index;
                        this.pagingLinks.push(item)
                        nexPage = index;
                    }
                }
                const prevPage = startIndex - 1;
                this.prevPage = (prevPage<1)?1:prevPage;
                this.nextPage = nexPage;
                this.lastPage = pagingEnd;
            }
        },
        pagingMove(cmd) {
            let page = 1;
            switch (cmd) {
                case 'first':
                    page = 1;
                    break;
                case 'prev':
                    page = this.prevPage;
                    break;
                case 'next':
                    page = (this.nextPage < this.lastPage) ? (this.nextPage + 1) : this.lastPage;
                    break;
                case 'last':
                    page = this.lastPage;
                    break;
            }
            this.$emit('pageMove', {page: page});
            this.serverReady = true;
            scrollTo(0,0)

        },
        pagingDefaultMove(page) {
            this.$emit('pageMove', {page: page});
            this.serverReady = true;
            scrollTo(0,0)

        },
        updateSort(id, sort) {
            for (let data of this.fixedHeaderDatas) {
                data.sortAsc = '';
                data.sortDesc = '';
            }
            for (let data of this.headerDatas) {
                data.sortAsc = '';
                data.sortDesc = '';
            }
            this.$emit('parentEvent', {type: 'dataSort', sort: {field: id, sort: sort}})
            this.serverReady = true;


        },
        headerSort(index, sort) {
            let item = this.headerDatas[index];
            this.updateSort(item.id, sort);

            item.sortAsc = (sort == 'asc') ? 'active' : ''
            item.sortDesc = (sort == 'desc') ? 'active' : ''
            this.$set(this.headerDatas, index, item)


        },
        fixHeaderSort(index, sort) {
            let item = this.fixedHeaderDatas[index];
            this.updateSort(item.id, sort);

            item.sortAsc = (sort == 'asc') ? 'active' : ''
            item.sortDesc = (sort == 'desc') ? 'active' : ''
            this.$set(this.fixedHeaderDatas, index, item)

        },

    },
    mounted() {
        JQ('html,body').on('mouseup', () => {
            if (this.scrollMove) this.scrollMove = false;
            if (this.divideMove) this.divideMove = false;
        });
        const obj = this;
        JQ('#scrollBarHander').on({
            'mousedown': function (e) {
                obj.scrollMove = true;
                obj.scrollFirstX = e.offsetX;


            },
            'mousemove': function (e) {
                let left = JQ('#headerTargetTable').scrollLeft();
                let x = obj.scrollFirstX - e.offsetX;
                let checkFlag = false;

                if (x >= 0) {
                    if (parseInt(left + JQ('#headerTargetTable').width()) >= parseInt(obj.scrollWidthNumber)) {
                        checkFlag = true;
                        obj.scrollFirstX = e.offsetX;
                    }
                }

                if (obj.scrollMove && !checkFlag) {

                    JQ('#targetTable').scrollLeft(left + x)
                    JQ('#headerTargetTable').scrollLeft(left + x)

                }

            },
            'mouseup': function (e) {
                obj.scrollMove = false;
            }
        })
        let layoutLeft = JQ('#table-layout-container').offset().left;

        let tableWidth = JQ('#table-layout-container').width();
        JQ('#table-layout-container').on({
            'mousemove': function (e) {
                if (obj.divideMove) {
                    let x = parseInt(e.clientX) - parseInt(layoutLeft);
                    let checkFlag = false;
                    let scrollWidthLimit = tableWidth - x;
                    if (obj.orgFixedWidth < x || obj.scrollWidthNumber < scrollWidthLimit) {
                        checkFlag = true;
                    }

                    if (obj.divideMove && !checkFlag) {
                        obj.fixedWidth = x;
                    }
                }

            },
            'mouseup': function (e) {
                if (obj.divideMove) obj.divideMove = false;
            }
        })

        JQ('#fixed-divide').on({
            'mousedown': function (e) {
                obj.divideMove = true;
                obj.divideFirstX = obj.fixedWidth;


            },

        })
    },
    updated() {
    }

}
</script>

