        }
    }

    .paging-row {
        margin-top: 30px;
        display: flex;
        justify-content: center;

        .paging-ul {
            padding: 0;
            margin: 0;
            display: flex;

            li {
                width: 26px;
                height: 26px;
                border-radius: 50%;
                border: solid 1px #999;
                margin: 0 3px;
                cursor: pointer;
                text-align: center;
                line-height: 22px;
                font-size: 12px;

                &.active {
                    background: #880E4F;
                    border: solid 1px #880E4F;
                    color: #fff;
                }

                &.d-page {
                    line-height: 24px;

                    &.active {
                        background: #880E4F;
                        border: solid 1px #880E4F;
                        color: #fff;
                    }
                }

            }
        }

